@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700,800);
.circuit {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.circuit-svg {
  display: block;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(1.1, 1.1) perspective(250px) rotateX(2deg) rotateY(3deg) rotateZ(-2deg);
          transform: scale(1.1, 1.1) perspective(250px) rotateX(2deg) rotateY(3deg) rotateZ(-2deg); }

.circuit-dot {
  fill: #102b44; }

.circuit-worm {
  stroke-width: 5px;
  stroke: #122f4b;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: transparent; }

.foreground-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; }

.foreground-svg {
  display: block;
  width: 100%;
  height: 100%;
  max-height: 80vw;
  overflow: visible; }
  .foreground-svg .lower-path {
    fill: #fffbfa; }
  .foreground-svg .upper-path {
    fill: tomato; }

.foreground-text {
  position: absolute;
  top: 20%;
  left: 8%;
  color: #fffbfa;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .foreground-text .title {
    background-color: tomato;
    border-bottom: 8px solid #eb5339;
    font-size: 80px;
    line-height: 80px;
    font-weight: 700;
    padding: 25px 30px;
    text-transform: uppercase; }
  .foreground-text .subtitle {
    margin-top: 30px;
    background-color: #122f4b;
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
    padding: 10px 30px;
    text-transform: uppercase; }

@media only screen and (max-width: 1024px) {
  .foreground-text .title {
    font-size: 50px;
    line-height: 50px;
    padding: 15px 20px; }
  .foreground-text .subtitle {
    font-size: 20px;
    line-height: 22px;
    padding: 10px 20px; } }

@media only screen and (max-width: 768px) {
  .foreground-text .title {
    font-size: 32px;
    line-height: 36px;
    padding: 10px 15px; }
  .foreground-text .subtitle {
    font-size: 18px;
    line-height: 22px;
    padding: 10px 15px; } }

html {
  min-height: 100%;
  height: 100%; }

body {
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: #082036;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

#root {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative; }

